<template>
    <div> 
        <p class="property"> LIGHT INTENSITY </p>
        <p class="value"> {{lux_index}}</p>
        <p class="rating"> {{lux_rating}} </p>
        <div> <span class="indicator"></span> </div>
    </div>
</template>

<script>
export default {
    props: ['luxData'],
    data() {
        return {
            lux_index: 0, 
            lux_rating: 'Night',
            lux_indicator: 100,
            timer: ''
        }
    },
    created(){
        console.log("lux created");
        this.timer = setInterval(this.updateLux, 1000);  //simulator
    },
    beforeDestroy(){
        this.cancelAutoUpdate();  //simulator
    },
    methods: {
        updateLux(){
            this.lux_index = Math.floor(Math.random() * (30000 - 20 +1)) + 20;  //simulator
            // if(this.lux_index < 100){
            //     this.lux_rating = 'Home Lighting';
            // } else if(this.lux_index < 500){
            //     this.lux_rating = 'Office Lighting';
            // } else if(this.lux_index < 1000){
            //     this.lux_rating = 'Studio Lighting';
            // } else if(this.lux_index < 10000){
            //     this.lux_rating = 'Day Lighting';
            // } else {
            //     this.lux_rating = 'Direct Sunlight';
            // }

            this.lux_index < 0.02 ? this.lux_rating = 'Night' :
            this.lux_index < 1 ? this.lux_rating = 'Moonlight' :
            this.lux_index < 1.1 ? this.lux_rating = 'Candle' :
            this.lux_index < 100 ? this.lux_rating = 'Street Lighting' :
            this.lux_index < 1000.1 ? this.lux_rating = 'Home Lighting' :
            this.lux_index < 10000.1 ? this.lux_rating = 'Overcast Sky' :
            this.lux_index < 100000.1 ? this.lux_rating = 'Sunny with Clouds' :
            this.lux_rating = 'Bright Sunlight';

            this.lux_indicator = 255 * this.lux_index / 30000;
            this.$parent.syncLuxLevel(this.lux_index);
        },
        cancelAutoUpdate(){
            clearInterval(this.timer);
        }
    },
    //sensor
    // watch: {
    //     luxData() {
    //       this.lux_index = this.luxData;
    //       this.updateLux();
    //     }
    // }
    //sensor
}
</script>

<style scoped>
     .property {
        margin: 0;
        margin-top: 20px;
        font-size: 14px;
    }
    .value {
        margin: 0;
        margin-top: 10px;
        font-size: 38px;
    }
    .rating {
        margin: 0;
        margin-top: 5px;
        margin-bottom: 5px;
        font-size: 14px;
    }
    .indicator {
        height: 15px;
        width: 15px;
        background-color: yellow;
        border-radius: 50%;
        display: table;
        margin: 0 auto;
    }
</style>