<template>
    <div 
        :id="id"
        :clone="clone"
        class="boardX"
        @dragover.prevent 
        @drop.stop="drop">
        <slot/>
    </div>
</template>

<script>
export default {
    data(){
        return {
            lastChild: ''
        }
    },
    props: ['id', 'clone'],
    methods: {
        drop(e) {
            var dropZone = e.target;
            const card_id = e.dataTransfer.getData('card_id');
            var card;
            if(this.clone == true){
                var board = document.getElementById(this.id);
                if(board.childNodes.length!=0){
                    this.$parent.eraseCardBoard(board.childNodes[0].id, this.id);
                    board.removeChild(board.childNodes[0]);
                }
                card = document.getElementById(card_id).cloneNode(true);
                card.id = card.id + "-" + this.id;
            } else {
                card = document.getElementById(card_id);
            }
            card.style.display = "inline-block";
            dropZone.appendChild(card);
            this.$parent.markCardBoard(card.id, this.id);
        },
        remove(e){
            const card_id = e.dataTransfer.getData('card_id');
            document.getElementById(card_id).remove();
        }
    }
}
</script>

