<template>
   <div 
        :id="id"
        class="tileX"
        :draggable="draggable"
        @click="selectMaterial"
        @dragstart = "dragStart" 
        @dragover.stop>
        <slot/>
    </div>
</template>

<script>
export default {
    props: ['id', 'draggable'],
    methods: {
        dragStart(e){
            const cardId = e.target.id;
            e.dataTransfer.setData('card_id',cardId);
            const boardId = document.getElementById(cardId).parentElement.id;
            // this.$parent.eraseCardBoard(cardId, boardId);
            this.$root.$emit('dragCard', cardId, boardId);
        },
        selectMaterial(){
            this.$emit('select-material');
        }
    }
}
</script>