<script>
  import Reveal from 'reveal.js/js/reveal'
  import learn from '@/components/Learning_uv'
  import Board from '@/components/Board'
  import Card from '@/components/Card'
  import Serial from '@/components/getSerial'
  import UVPreview from '@/components/UVSensor'
  import LUXPreview from '@/components/LUXSensor'

  export default{
    data() {
      return {
        learn_radio: '',
        materiallist:['Kraft_Paper', 'Red_Paper', 'Clear_Plastic', 'Paper_Napkin', 'Green_Cellophane', 'Clear_Plastic_+Sunblock'],
        zallylist:['happy_sally', 'sad_sally', 'sunburnt_sally'],
        checklist: [],
        radio: '',
        low_board: 'Blocks very little UV',
        // medium_board: 'medium protection',
        high_board: 'Blocks a lot of UV',
        input_material: '',
        input_reason: '',
        input_number: '',
        warning_text: '',
        input_evidence: '',
        input_finding: '',
        uvData: null,
        luxData: null,
        reData: null,
        add_visible: false,
        multi_able: true,
        radio_able: true,
        next_able: true,
        learn_able: false,
        number_able: true,
        number_wrong: false,
        sensor_connected: false,
        number_success: false,
        over_able: true,
        hypo_result: '',
        report: '',
        luxPreview: false,
        uvPreview: false,
        uvSafeMaterials: [],
        uvHazardMaterials: [],
        brighterMaterials: [],
        darkishMaterials: [],
        bestMaterial: "",
        betterMaterial: "",
        addingNew: false,
        newMaterial: "",
        uvLevel: 0,
        luxLevel: 0,
        uvDataBaseline:[],
        uvDataMaterial1:[],
        uvDataMaterial2:[],
        luxDataBaseline:[],
        luxDataMaterial1:[],
        luxDataMaterial2:[],
        selectedSensor: 'uv',
        testingMaterial: '0',
        luxColor: '#000000',
        uvColor: '#000000',
        printMode: false,
        discussionForm:{
          dataShow: "",
          hypothesisSupported: "",
          evidenceSuggests: "",
          findingsSurprised: ""
        },
        sallyIfMaterial: {
          happy: [],
          sad: [],
          sunburnt: [],
        },
        sallyTrueMaterials: {
          happy: [],
          sad: [],
          sunburnt: [],
        },
        hypothesis: {
          bestMaterial: [],
          supportClause1: "",
          supportClause2: ""
        },
        observation: {
          bestMaterial: [],
          supportClause1: "",
          supportClause2: ""
        },
        zallyCount:0,
        finalZallyCount:0,
        editMaterial: "",
        editedMaterial: "",
        isVideoPlaying: false,
        video:null,
        solo: false,
        highlightMaterialSelect: false,
      }
    },
    components: {
      'learn': learn,
      'board':Board,
      'card': Card,
      // 'serial': Serial, //sensor
      'uvPreview': UVPreview,
      'luxPreview': LUXPreview
    },
    mounted() {
      Reveal.initialize({
        // keyboard: false,
        controls: false,
        overview: true,
        touch: false,
        progress: false,
        width:1240,
        height: 700,
        center: false
      });
      Reveal.addEventListener('hypo1', function() {
        console.log('here');
      });
      Reveal.addEventListener('hypo2', function() {
        console.log('here');
      });
      this.$root.$on('dragCard', (card, board)=>{
        // this.eraseCardBoard(card, board);
      });
    },
    methods: {
      Next() {
        this.ResetAnyPlayingVideos();
        if(this.solo){
          this.Jump(12,1);
        }
        Reveal.right();
      },
      Left() {
        this.ResetAnyPlayingVideos();
        Reveal.left();
      },
      Down() {
        Reveal.down();
      },
      Up() {
        Reveal.up();
      },
      Jump(h,v) {
        this.ResetAnyPlayingVideos();
        Reveal.slide(h,v);
      },
      ResetAnyPlayingVideos(){
        if(this.video != null){
          var video = this.video;
          video.load();
        }
        this.isVideoPlaying = false;
        this.video = null;
      },
      ConnectSensor(){   
        console.log("conencting to sensor");
        this.sensor_connected = true;
      },
      PlayIntro(videoID){
        this.isVideoPlaying = true;
        this.video = document.getElementById(videoID);
        this.video.play();
        this.video.addEventListener('ended', this.onVideoEnded);
      },
      pauseVideo(videoID){
        this.isVideoPlaying = false;
        this.video.pause();
      },
      onVideoEnded(){
        console.log("video ended ");
        this.isVideoPlaying = false;
        this.video = null;
      },
      getImgUrl(name) {
        try{
          return require('../assets/'+ name +".png")
        } catch(e){
          return require('../assets/No_Roof.png')
        }
      },
      formatText(text){
        if(text!=null){
          return text.split('_').join('\n');
        }
        return "";
      },
      markCardBoard(card, board){
        const material = card.split('-')[2];
        if(board == "board-uv-low"){
          this.bestMaterial = material;
          if(this.uvSafeMaterials.includes(material)){
            this.uvSafeMaterials.splice(this.uvSafeMaterials.indexOf(material), 1);
          }
          if(!this.uvHazardMaterials.includes(material)){
            this.uvHazardMaterials.push(material);
          }
        }
        else if(board == "board-uv-high"){
          this.betterMaterial = material;
          if(!this.uvSafeMaterials.includes(material)){
            this.uvSafeMaterials.push(material);
          }
          if(this.uvHazardMaterials.includes(material)){
            this.uvHazardMaterials.splice(this.uvHazardMaterials.indexOf(material), 1);
          }
        }
        else if(board == "board-lux-high"){
          this.bestMaterial = material;
          if(!this.brighterMaterials.includes(material)){
            this.brighterMaterials.push(material);
          }
          if(this.darkishMaterials.includes(material)){
            this.darkishMaterials.splice(this.darkishMaterials.indexOf(material), 1);
          }
        }
        else if(board == "board-lux-low"){
          this.betterMaterial = material;
          if(this.brighterMaterials.includes(material)){
            this.brighterMaterials.splice(this.brighterMaterials.indexOf(material), 1);
          }
          if(!this.darkishMaterials.includes(material)){
            this.darkishMaterials.push(material);
          }
        }        
        else if(board == "stock1"){
          if(this.uvSafeMaterials.includes(material)){
            this.uvSafeMaterials.splice(this.uvSafeMaterials.indexOf(material), 1);
          }
          if(this.uvHazardMaterials.includes(material)){
            this.uvHazardMaterials.splice(this.uvHazardMaterials.indexOf(material), 1);
          }
        } else if(board == "stock2"){
          if(this.brighterMaterials.includes(material)){
            this.brighterMaterials.splice(this.brighterMaterials.indexOf(material), 1);
          }
          if(this.darkishMaterials.includes(material)){
            this.darkishMaterials.splice(this.darkishMaterials.indexOf(material), 1);
          }
        } 

        if(board.includes("zally")){
          var childCount = document.getElementById(board).childNodes.length;
          if(childCount == 1){
            if(card.includes("hypo")){this.zallyCount++;}
            if(card.includes("result")){this.finalZallyCount++;}
          } else {
            // alert(board+" "+ card);
            var bNode = document.getElementById(board);
            var cNode = document.getElementById(card.replace("-"+board, ""));
            bNode.appendChild(cNode.cloneNode(true));
          }
          var zallyState = card.split('-')[1].split('_')[0];
          var zallyMaterial = board.split('-')[2];
          if(card.includes("hypo")){
            this.sallyIfMaterial[zallyState].includes(zallyMaterial)? this.doNothing():
            this.sallyIfMaterial[zallyState].push(zallyMaterial);
          } else if(card.includes("result")){
            this.sallyTrueMaterials[zallyState].includes(zallyMaterial)? this.doNothing():
            this.sallyTrueMaterials[zallyState].push(zallyMaterial);
          }     
        }  
      },
      eraseCardBoard(card, board){
        var material = board.replace("zally-if-","");
        var state = card.split('-')[1].split('_')[0];     
        if(card.includes("hypo")){
          var materialIndex = this.sallyIfMaterial[state].indexOf(material);
          this.sallyIfMaterial[state].splice(materialIndex, 1);
        } else {
          var materialIndex = this.sallyTrueMaterials[state].indexOf(material);
          this.sallyTrueMaterials[state].splice(materialIndex, 1);
        }
      },
      avg(list){
        const sum = list.reduce((a, b) => Number(a) + Number(b), 0);
