        if(this.luxDataMaterial1.length==4){this.luxDataMaterial1.pop();}
        if(this.luxDataMaterial2.length==4){this.luxDataMaterial2.pop();}

        this.uvDataBaseline.push(this.avg(this.uvDataBaseline));// = 1;//this.avg(this.uvDataBaseline);
        this.uvDataMaterial1.push(this.avg(this.uvDataMaterial1));// = 1;//this.avg(this.uvDataMaterial1);
