          //simulator
          case "0": this.saveReading(this.uvDataBaseline, this.uvLevel);this.saveReading(this.luxDataBaseline, this.luxLevel); break;
          case "1": this.saveReading(this.uvDataMaterial1, this.uvLevel); this.saveReading(this.luxDataMaterial1, this.luxLevel);break;
