      generateHypothesis(){
        
        var happyMaterials = this.sallyIfMaterial.happy;
        var uvSafeMaterials = this.uvSafeMaterials;
        var brighterMaterials = this.brighterMaterials;

        var union = [happyMaterials, uvSafeMaterials, brighterMaterials];
        var intersection = union.reduce((a, b) => a.filter(c => b.includes(c)));

        if(intersection.length==2){
          this.hypothesis.bestMaterial = intersection;
          this.hypothesis.supportClause1 = "they will block more UV light";
          this.hypothesis.supportClause2 = "they will let more visible light through";
          this.Next(); return;
        } else if(intersection.length==1){
          this.hypothesis.bestMaterial = intersection;
          this.hypothesis.supportClause1 = "it will block more UV light";
          this.hypothesis.supportClause2 = "it will let more visible light through";
          this.Next(); return;
        }

        if(intersection.length==0){
          union = [happyMaterials, uvSafeMaterials];
          intersection = union.reduce((a, b) => a.filter(c => b.includes(c)));
        }
        if(intersection.length==2){
          this.hypothesis.bestMaterial = intersection;
          this.hypothesis.supportClause1 = "they will block more UV light";
          this.hypothesis.supportClause2 = "they will let less visible light through";
          Next(); return;
        } else if(intersection.length==1){
          this.hypothesis.bestMaterial = intersection;
          this.hypothesis.supportClause1 = "it will block more UV light";
          this.hypothesis.supportClause2 = "it will let less visible light through";
          this.Next(); return;
        }

        if(intersection.length==0){
          union = [happyMaterials, brighterMaterials];
          intersection = union.reduce((a, b) => a.filter(c => b.includes(c)));
        }
        if(intersection.length==2){
          this.hypothesis.bestMaterial = intersection;
          this.hypothesis.supportClause1 = "they will block less UV light";
          this.hypothesis.supportClause2 = "they will let more visible light through";
          this.Next(); return;
        } else if(intersection.length==1){
          this.hypothesis.bestMaterial = intersection;
          this.hypothesis.supportClause1 = "it will block less UV light";
          this.hypothesis.supportClause2 = "it will let more visible light through";
          this.Next(); return;
        }

        if(happyMaterials.length>1){
          this.hypothesis.bestMaterial = happyMaterials;
          this.hypothesis.supportClause1 = "they will block less UV light";
          this.hypothesis.supportClause2 = "they will let less visible light through";
          this.Next(); return;
        } else if(happyMaterials.length==1){
          this.hypothesis.bestMaterial = happyMaterials;
          this.hypothesis.supportClause1 = "it will block less UV light";
          this.hypothesis.supportClause2 = "it will let less visible light through";
          this.Next(); return;
        }
         
        this.Next();
      },
      generateSupportClause(material){
        var clause = "";
        this.uvSafeMaterials.includes(material)? clause+="blocks a lot of UV and ": clause+="blocks very little UV and ";
        this.brighterMaterials.includes(material)? clause+="lets in a lot of visible light": clause+="lets in very little visible light";
        return clause;
      },
