        }, 1000);
      }
    },
    computed: {
      arrowStyle () {
        return { transform: 'translate(0,' + (this.uvLevel*15 - 225) + 'px)', filter: 'hue-rotate('+(15-this.uvLevel)*10+'deg)'}
      },
      dataCollectionComplete () {
        var totalDataCount = this.uvDataBaseline.length + this.uvDataMaterial1.length + this.uvDataMaterial2.length + this.luxDataBaseline.length + this.luxDataMaterial1.length + this.luxDataMaterial2.length;
        var hasNoBlankSlots = [].concat(this.uvDataBaseline, this.uvDataMaterial1, this.uvDataMaterial2, this.luxDataBaseline, this.luxDataMaterial1, this.luxDataMaterial2).indexOf("") === -1;
        return totalDataCount >= 18 && hasNoBlankSlots;
