<template>
    <div> 
        <p class="property"> UV INDEX </p>
        <p class="value"> {{uv_index}}</p>
        <p class="rating"> {{uv_rating}} </p>
        <div> <span id="uv-color" v-bind:style="{backgroundColor: uv_color}" class="indicator"></span> </div>
    </div>
</template>

<script>
export default {
    props: ['uvData'],
    data() {
        return {
            uv_index: 0, 
            uv_rating: 'Low',
            uv_color: 'green',
            timer: ''
        }
    },
    created(){
        console.log("uv created");
        this.timer = setInterval(this.updateUv, 1000); //simulator
    },
    beforeDestroy(){
        this.cancelAutoUpdate();  //simulator
    },
    methods: {
        updateUv(){
            this.uv_index = Math.floor(Math.random() * (15 - 1 +1)) + 1;  //simulator
            // if(this.uv_index <= 2){
            //     this.uv_rating = "Low";
            //     this.uv_color = "green";
            // } else if( this.uv_index <= 7){
            //     this.uv_rating = "Medium";
            //     this.uv_color = "orange";
            // } else if( this.uv_index <= 10){
            //     this.uv_rating = "High";
            //     this.uv_color = "red";
            // } else {
            //     this.uv_rating = "Extreme";
            //     this.uv_color = "purple";
            // }
            this.uv_index <=2 ? this.setRating('Low', 'green') :
            this.uv_index <=7 ? this.setRating('Medium', 'orange') :
            this.uv_index <=10 ? this.setRating('High', 'red') :
            this.setRating('Extreme', 'purple');

            this.$parent.syncUVLevel(this.uv_index);
        },
        setRating(rating, color){
            this.uv_rating = rating;
            this.uv_color = color;
        },
        cancelAutoUpdate(){
            clearInterval(this.timer);
        }
    },
    //sensor
    // watch: { 
    //     uvData() {
    //         this.uv_index = this.uvData;
    //         this.updateUv();
    //     }
    // }
    //sensor
}
</script>

<style scoped> 
    .property {
        margin: 0;
        margin-top: 20px;
        font-size: 14px;
    }
    .value {
        margin: 0;
        margin-top: 10px;
        font-size: 38px;
    }
    .rating {
        margin: 0;
        margin-top: 5px;
        margin-bottom: 5px;
        font-size: 14px;
    }
    .indicator {
        height: 15px;
        width: 15px;
        background-color: green;
        border-radius: 50%;
        display: table;
        margin: 0 auto;
    }
</style>